import React from 'react';
import BeerCard from './BeerCard';
import FilterBar from './FilterBar';

export default function SearchResults(props) {
  const beerCards = props.beers.map((beer) => (
    <BeerCard
      key={beer.id}
      beer={beer}
      onHeartClick={(id) => props.onHeartClick(id)}
      onCardClick={(id) => props.onCardClick(id)}
    />
  ));
  const hasEvenCards = props.beers.length % 2 === 0;
  const resultsCount = props.beers.length;
  const filterTags = props.refinedFilters.map((filter, i) => (
    <span key={i} className={`filter-tag${i === 0 ? ' filter-tag--first' : ''}`}>
      {filter}
    </span>
  ));
  return (
    <section className="section search-results">
      <header className="section__header">
        {(props.searchQuery !== '' || props.showFilteredList !== false) && (
          <h1 className="section__title section__title--sm">
            <span className="search-results__count">{resultsCount}</span>
            {`${resultsCount === 1 ? 'beer' : 'beers'} tagged`}
            {props.searchQuery !== '' && (
              <span className="search-results__query">{props.searchQuery}</span>
            )}
            {props.showFilteredList && filterTags}
          </h1>
        )}
        {props.filterBar}
      </header>
      <div className={`card-grid ${hasEvenCards ? 'has-even-cards' : 'has-odd-cards'}`}>
        {beerCards}
      </div>
    </section>
  );
}
